.costos-container {
  padding: 40px;
  color: #333;
}

.costos-titulo {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 30px;
}

.costos-filtros {
  display: flex;
  gap: 25px;
  align-items: flex-end;
  margin-bottom: 40px;
}

.campo {
  display: flex;
  flex-direction: column;
}

.campo label {
  font-weight: 600;
  margin-bottom: 4px;
}

.campo select,
.campo input[type="month"] {
  padding: 6px 10px;
  border: 1px solid #bbb;
  border-radius: 6px;
  width: 180px;
}

/* Bot√≥n buscar */
.btn-buscar {
  background: #3b82f6;
  color: white;
  border: none;
  padding: 8px 20px;
  border-radius: 6px;
  cursor: pointer;
}

.btn-buscar:hover {
  background: #2563eb;
}

.tabla-wrapper {
  border: 1px solid #ddd;
  border-radius: 6px;
  overflow: hidden;
  width: 100%;
}

.tabla-costos {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}

.tabla-costos th {
  background: #f5f5f5;
  padding: 10px;
  text-align: left;
  border-right: 1px solid #ddd;
}

.tabla-costos td {
  padding: 10px;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #ddd;
}

.tabla-costos tr:nth-child(even) {
  background: #fafafa;
}

.fila-total {
  background: #f0f0f0;
  font-weight: bold;
}

.total-label {
  text-align: right;
}

.tabla-costos-wrapper {
  width: 100%;
  overflow-x: auto;
  padding-bottom: 10px;
}

.tabla-costos {
  min-width: max-content;
  border-collapse: collapse;
}
