.header-eleccion-recurso {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  width: 1000px;
  margin: 0 auto;
  text-align: center;
  font-size: 35px;
}

.login-container {
  display: flex;
  height: 100vh;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}

/* IZQUIERDA - BACKGROUND */
.login-left {
  flex: 1;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 50px;
  display: flex;
  align-items: center;
}

.login-title {
  color: white;
  font-size: 42px;
  font-weight: bold;
  line-height: 1.2;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

/* DERECHA - FORMULARIO */
.login-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 80px;
}

.login-welcome {
  font-size: 32px;
  margin-bottom: 10px;
}

.login-subtitle {
  color: #555;
  margin-bottom: 25px;
}

.login-input {
  padding: 12px;
  margin-bottom: 15px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 16px;
  width: 80%;
}

.login-button {
  padding: 12px;
  width: 150px;
  background-color: #0a4abf;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 18px;
  margin-top: 10px;
  transition: 0.2s;
}

.login-button:hover {
  background-color: #083a97;
}
